function clear() {
  geneData.data = [];
}

function edit(row){
  let list = toRaw(row)
  form._id = list["_id"]
  form.patient = list["Patient"]
  form.gene = list["Gene(s)"]
  form.inheritance= list["Inheri-tance"]
