  form.genotype= list["Genotype"]
  form.disorder= list["Disorder (MIM#)"]
  form.classification= list["Classification"]
  form.origin = list["origin"]
  dialogEditVisible.value = true
}

function editSubmit(){
  fetch('http://localhost:3000/update', {
    method: 'post',
