      type: "success",
    });
    dialogEditVisible.value = false
    
  }).then(()=>{
    search()
  })
  .catch(function(data) {
    console.log(data)
    ElMessage({
      showClose: true,
      message: "Someting Wrong",
      type: "error",
    });
  })
}

